div.mainContainerSection4 {
    height: 50vh;
    border-top: 5px solid black;
    background-position: center;
    background-repeat: no-repeat;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: rgba(0,0,0,0.5);
}
div.mainContainerSection4 div.shadowMainContainer h2 {
    margin: 0;
    padding: 0;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
    font-size: 50px;
    cursor: pointer;
}

div.mainContainerSection4 div.shadowMainContainer div.leftContainer {
    width: 50vw;
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.leftBox {
    width: 80%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.leftBox div.shadowLeftBox {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, rgba(4,104,191,0), black);
    display: flex;
    justify-content: center;
    align-items: center;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.triangleLeft {
    width: 0;
    height: 0;
    border-bottom: 50vh solid transparent;
    border-left: 5vw solid black;
}

div.mainContainerSection4 div.shadowMainContainer div.rightContainer {
    width: 50vw;
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
div.mainContainerSection4 div.shadowMainContainer div.rightContainer div.triangleRight {
    width: 0;
    height: 0;
    border-top: 50vh solid transparent;
    border-right: 5vw solid black;
}
div.mainContainerSection4 div.shadowMainContainer div.rightContainer div.rightBox {
    width: 80%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer div.rightContainer div.rightBox div.shadowRightBox {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to left, rgba(4,104,191,0), black);
    display: flex;
    justify-content: center;
    align-items: center;
}

