div.mainContainerSection2 {
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    background-size: 50vw 50vh;
    background-position: right center;
    background-repeat: no-repeat;
    border-top: 5px solid black;
    transition: 0.5s;
}
div.mainContainerSection2 div.leftBox {
    width: 48vw;
    height: 100%;
    background-image: linear-gradient(to right, rgba(4,104,191,0.80), rgba(4, 104, 191,1));
    /* background: rgb(230,230,232);
    background: linear-gradient(270deg, rgba(4,104,191,1) 15%, rgba(0,0,0,1) 90%); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 3vw;
}
div.mainContainerSection2 div.leftBox h1 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection2 div.leftBox p {
    font-size: 20px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection2 div.leftBox button {
    padding: 10px 20px;
    align-self: flex-end;
    margin-right: 10vw;
    margin-bottom: 5vh;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    background-color: transparent;
    color: whitesmoke;
    border: 2px solid whitesmoke;
    cursor: pointer;
    box-shadow: 10px 10px 10px black;
}
div.mainContainerSection2 div.leftBox button:hover {
    color: rgba(4, 104, 191, 1);
    border: 2px solid rgba(4, 104, 191, 1);
    background-color: whitesmoke;
    transition: 0.5s;
    font-weight: bold;
    box-shadow: 10px 10px 10px white;
}
div.mainContainerSection2 div.triangle {
    width: 0;
    height: 0;
    border-bottom: 50vh solid transparent;
    border-left: 10vw solid rgba(4, 104, 191, 1);
}