div.mainContainerSection3 {
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-size: 50vw 45vh;
    background-position: right center;
    background-repeat: no-repeat;
    border-top: 5px solid black;
}
div.mainContainerSection3 div.logoTypesContainer {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
div.mainContainerSection3 div.logoTypesContainer div.logoTypesBelt {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
div.mainContainerSection3 div.triangle {
    width: 0;
    height: 0;
    border-bottom: 50vh solid transparent;
    border-right: 10vw solid rgba(4, 104, 191, 0.80);
}
div.mainContainerSection3 div.rightBox {
    width: 35vw;
    height: 100%;
    background-image: linear-gradient(to right, rgba(4,104,191,0.80), rgba(4, 104, 191,1));
    /* background: rgb(230,230,232);
    background: linear-gradient(90deg, rgba(4,104,191,0.80) 15%, rgba(0,0,0,1) 90%); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 3vw;
}
div.mainContainerSection3 div.rightBox h1 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection3 div.rightBox p {
    font-size: 20px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
    margin-right: 3vw;
}

