div.mainContainerFooter {
    height: 8vh;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

div.mainContainerFooter div.copyright {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.mainContainerFooter div.copyright p {
    font-size: 15px;
    color: black;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}

div.mainContainerFooter div.author {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.mainContainerFooter div.author p {
    margin: 0;
    font-size: 15px;
    color: black;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}