@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

div.menuSectionContainer {
    width: max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding-right: 1vw;
    padding-left: 1vw;
}
div.menuSectionContainer p.text {
    font-size: 16px;
    color: #010D26;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}
div.menuSectionContainer:hover p.text {
    color: rgba(4, 104, 191, 1);
    transition: 0.5s;
}