@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
div.mainMenuContainer {
    width: 100%;
    height: 10vh;
    background-color: rgba(255,255,255,0.9);
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: fixed; 
}
div.mainMenuContainer div.logo {
    width: 10%;
    height: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}
div.mainMenuContainer div.menu {
    height: 100%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
div.mainMenuContainer div.menu > div {
    border-right: 2px solid #010D26;
}
div.mainMenuContainer div.menu > div:last-child{
    border: none;
}
div.menuSectionContainer {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding-right: 1vw;
    padding-left: 1vw;
}
div.menuSectionContainer p.text {
    font-size: 16px;
    color: #010D26;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}
div.menuSectionContainer:hover p.text {
    color: rgba(4, 104, 191, 1);
    transition: 0.5s;
}
div.whiteTopBelt {
    height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
div.mainContainerSection1 {
    height: 65vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    background-size: contain;
    background-position: right center;
    background-repeat: no-repeat;
}
div.mainContainerSection1 div.leftBox {
    width: 45vw;
    height: 100%;
    background-image: linear-gradient(to left, rgba(4,104,191,0.80), rgba(4, 104, 191,1));
    /* background: rgb(230,230,232);
    background: linear-gradient(270deg, rgba(4,104,191,0.80) 15%, rgba(0,0,0,1) 90%); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 3vw;
}
div.mainContainerSection1 div.triangle {
    width: 0;
    height: 0;
    border-top: 65vh solid transparent;
    border-left: 30vw solid rgba(4, 104, 191, 0.80);
}
div.mainContainerSection1 div.leftBox h1 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection1 div.leftBox p {
    font-size: 20px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}


div.mainContainerSection2 {
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    background-size: 50vw 50vh;
    background-position: right center;
    background-repeat: no-repeat;
    border-top: 5px solid black;
    transition: 0.5s;
}
div.mainContainerSection2 div.leftBox {
    width: 48vw;
    height: 100%;
    background-image: linear-gradient(to right, rgba(4,104,191,0.80), rgba(4, 104, 191,1));
    /* background: rgb(230,230,232);
    background: linear-gradient(270deg, rgba(4,104,191,1) 15%, rgba(0,0,0,1) 90%); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 3vw;
}
div.mainContainerSection2 div.leftBox h1 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection2 div.leftBox p {
    font-size: 20px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection2 div.leftBox button {
    padding: 10px 20px;
    align-self: flex-end;
    margin-right: 10vw;
    margin-bottom: 5vh;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    background-color: transparent;
    color: whitesmoke;
    border: 2px solid whitesmoke;
    cursor: pointer;
    box-shadow: 10px 10px 10px black;
}
div.mainContainerSection2 div.leftBox button:hover {
    color: rgba(4, 104, 191, 1);
    border: 2px solid rgba(4, 104, 191, 1);
    background-color: whitesmoke;
    transition: 0.5s;
    font-weight: bold;
    box-shadow: 10px 10px 10px white;
}
div.mainContainerSection2 div.triangle {
    width: 0;
    height: 0;
    border-bottom: 50vh solid transparent;
    border-left: 10vw solid rgba(4, 104, 191, 1);
}
div.mainContainerSection3 {
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-size: 50vw 45vh;
    background-position: right center;
    background-repeat: no-repeat;
    border-top: 5px solid black;
}
div.mainContainerSection3 div.logoTypesContainer {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
div.mainContainerSection3 div.logoTypesContainer div.logoTypesBelt {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
div.mainContainerSection3 div.triangle {
    width: 0;
    height: 0;
    border-bottom: 50vh solid transparent;
    border-right: 10vw solid rgba(4, 104, 191, 0.80);
}
div.mainContainerSection3 div.rightBox {
    width: 35vw;
    height: 100%;
    background-image: linear-gradient(to right, rgba(4,104,191,0.80), rgba(4, 104, 191,1));
    /* background: rgb(230,230,232);
    background: linear-gradient(90deg, rgba(4,104,191,0.80) 15%, rgba(0,0,0,1) 90%); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 3vw;
}
div.mainContainerSection3 div.rightBox h1 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection3 div.rightBox p {
    font-size: 20px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
    margin-right: 3vw;
}


div.logoComp {
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 8vw;
    height: 8vw;
}
div.mainContainerSection4 {
    height: 50vh;
    border-top: 5px solid black;
    background-position: center;
    background-repeat: no-repeat;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: rgba(0,0,0,0.5);
}
div.mainContainerSection4 div.shadowMainContainer h2 {
    margin: 0;
    padding: 0;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
    font-size: 50px;
    cursor: pointer;
}

div.mainContainerSection4 div.shadowMainContainer div.leftContainer {
    width: 50vw;
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.leftBox {
    width: 80%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.leftBox div.shadowLeftBox {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, rgba(4,104,191,0), black);
    display: flex;
    justify-content: center;
    align-items: center;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.triangleLeft {
    width: 0;
    height: 0;
    border-bottom: 50vh solid transparent;
    border-left: 5vw solid black;
}

div.mainContainerSection4 div.shadowMainContainer div.rightContainer {
    width: 50vw;
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
div.mainContainerSection4 div.shadowMainContainer div.rightContainer div.triangleRight {
    width: 0;
    height: 0;
    border-top: 50vh solid transparent;
    border-right: 5vw solid black;
}
div.mainContainerSection4 div.shadowMainContainer div.rightContainer div.rightBox {
    width: 80%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer div.rightContainer div.rightBox div.shadowRightBox {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to left, rgba(4,104,191,0), black);
    display: flex;
    justify-content: center;
    align-items: center;
}


div.mainContainerSection5 {
    height: 40vh;
    background-image: linear-gradient(to left, rgba(0, 0, 0, 0.80), rgba(0, 0, 0, 1));
    border-top: 5px solid black;
}

div.mainContainerSection5 div.spriteBgc {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-position: left center;
    background-repeat: no-repeat;
    background-size: 50% 100%;
}

div.mainContainerSection5 div.rightBox {
    width: 35vw;
    height: 100%;
    background-image: linear-gradient(to right, rgba(4, 104, 191, 0.80), rgba(4, 104, 191, 1));
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 3vw;
}

div.mainContainerSection5 div.rightBox h1 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}

div.mainContainerSection5 div.rightBox button {
    padding: 10px 20px;
    align-self: flex-end;
    margin-right: 10vw;
    margin-bottom: 5vh;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    background-color: transparent;
    color: whitesmoke;
    border: 2px solid whitesmoke;
    cursor: pointer;
    box-shadow: 10px 10px 10px black;
}

div.mainContainerSection5 div.rightBox button:hover {
    color: rgba(4, 104, 191, 1);
    border: 2px solid rgba(4, 104, 191, 1);
    background-color: whitesmoke;
    transition: 0.5s;
    font-weight: bold;
    box-shadow: 10px 10px 10px white;
}

div.mainContainerSection5 div.triangle {
    width: 0;
    height: 0;
    border-top: 40vh solid transparent;
    border-right: 25vw solid rgba(4, 104, 191, 0.80);
}

div.mainContainerSection5 div.spriteContainer {
    flex-grow: 1;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
div.mainContainerFooter {
    height: 8vh;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

div.mainContainerFooter div.copyright {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.mainContainerFooter div.copyright p {
    font-size: 15px;
    color: black;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}

div.mainContainerFooter div.author {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.mainContainerFooter div.author p {
    margin: 0;
    font-size: 15px;
    color: black;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}
