@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

div.whiteTopBelt {
    height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
div.mainContainerSection1 {
    height: 65vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    background-size: contain;
    background-position: right center;
    background-repeat: no-repeat;
}
div.mainContainerSection1 div.leftBox {
    width: 45vw;
    height: 100%;
    background-image: linear-gradient(to left, rgba(4,104,191,0.80), rgba(4, 104, 191,1));
    /* background: rgb(230,230,232);
    background: linear-gradient(270deg, rgba(4,104,191,0.80) 15%, rgba(0,0,0,1) 90%); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 3vw;
}
div.mainContainerSection1 div.triangle {
    width: 0;
    height: 0;
    border-top: 65vh solid transparent;
    border-left: 30vw solid rgba(4, 104, 191, 0.80);
}
div.mainContainerSection1 div.leftBox h1 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.mainContainerSection1 div.leftBox p {
    font-size: 20px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}

