div.mainMenuContainer {
    width: 100%;
    height: 10vh;
    background-color: rgba(255,255,255,0.9);
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: fixed; 
}
div.mainMenuContainer div.logo {
    width: 10%;
    height: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}
div.mainMenuContainer div.menu {
    height: 100%;
    width: max-content;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
div.mainMenuContainer div.menu > div {
    border-right: 2px solid #010D26;
}
div.mainMenuContainer div.menu > div:last-child{
    border: none;
}